.App {
  position: relative;
  width: 100vw;
  /* height: 7100px; */

  background: white;
}
.logo {
  position: absolute;
  width: 200px;
  height: 32px;
  left: 70px;
  top: 40px;

  font-family: "Righteous", cursive;
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 30px;
  text-align: center;
  transition: all ease-in-out 0.3s;

  color: #000000;
}
.logo-circle {
  font-family: Righteous;
  padding: 10px;
  opacity: 90%;
  text-align: center;

  content: "S";
  position: fixed;
  bottom: 2vw;
  left: 2vw;
  display: block;
  height: 64px;
  width: 64px;
  border-radius: 50%;
  background-color: #333333;
  transform: scale(1.5);
  transition: all ease-in 0.3s;
  color: rgb(255, 62, 175);
  font-size: 24px;
  line-height: 3.5rem;
  font-weight: 100;
  z-index: 5;
}

.nav-menu {
  display: flex;

  position: fixed;
  width: 310px;
  height: 21px;
  right: 100px;
  top: 45px;
  font-family: Righteous;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 20px;

  text-align: center;
  justify-content: space-between;
  align-items: center;
  transition: all ease-in-out 0.3s;
  z-index: 5;
  letter-spacing: 2px;
}

.nav-menu-flipped {
  display: flex;

  position: fixed;
  width: 310px;
  height: 21px;
  right: 0px;
  top: 245px;
  font-family: Righteous;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;

  text-align: center;
  justify-content: space-between;
  align-items: center;
  transform: rotate(90deg);
  transition: all ease-in-out 0.3s;
  z-index: 5;
}

